<template>

<modal-report v-model:visible="modalFeedbackVisible"/>

<modal v-model:visible="modalConfirmationVisible" modalWidth="26em">
  <template #content>
    <div class="modal-text">Побачивши відповідь, ви вже не зможете робити нових спроб. Все одно продовжити?</div>
  </template>
  <template #buttons>
    <button class="button-small" :style="{ background: colorTheme }" @click="postAnswer()">Продовжити</button>
    <button class="button-small" :style="{ background: colorTheme }" @click="modalConfirmationVisible = false">Скасувати</button>
  </template>
</modal>

<modal v-model:visible="modalResultVisible" modalWidth="20em">
  <template #content>
    <div class="modal-header">Ваш результат</div>
    <div class="chart-wrapper">
      <chart-doughnut :data="[lessonPercentage, 100-lessonPercentage]" :colors="[mix3(lessonPercentage), '#00000015']">
        <div class="chart-percentage">{{ lessonPercentage }}%</div>
        <div class="chart-label">правильно</div>
      </chart-doughnut>
    </div>
  </template>
</modal>

<main-layout :backgroundImage="backgroundURL" backgroundOffset="10">

  <template #header>
    <div class="header-section">
      <div class="lesson-navbar">
        <router-link to="lessons" class="link">
          <span class="material-icons">arrow_back</span>&nbsp;
          Назад до списку тем
        </router-link>
        <span class="link" @click="modalFeedbackVisible = true">Помітили помилку?</span>
      </div>
      <div class="lesson-name">{{ lessonName }}</div>
      <div><pagination v-model='taskIndex' :pageMax='lesson.length' :pageLimit=10 :colors='paginationColors'/></div>
    </div>
  </template>

  <template #body>
    <div class="task-section">
      <div class="task-info">
        <span class="material-icons-outlined tablet" :style='{ color: task.completed ? mix3(task.percentage) : "#0005" }'>
          {{ !task.completed ? "not_started" : task.percentage == 100 ? "done" : task.percentage == 0 ? "close" : "done" }}
        </span>
        <span class="task-index">
          Завдання {{ taskIndex+1 }} з {{ lesson.length }}&nbsp;
        </span>
        <span class="tag1 desktop" :style='{ background: task.completed ? mix3(task.percentage) : "#0005" }'>
          {{ !task.completed ? "Без відповіді" : task.percentage == 100 ? "Правильно" : task.percentage == 0 ? "Помилка" : "Частково" }}
        </span>
        <!-- <span class="desktop-tablet" v-if="task.type == 'radio' && task.answersTotal > 3">
          Складність:&nbsp;
          <span class="task-complexity" :style="{ borderColor: mix3(100 - task.complexity*10), color: mix3(100 - task.complexity*10) }">{{ task.complexity }}</span>
        </span> -->
        <!-- <input type="checkbox" id="toggleTips" @click="$store.commit('toggleTips')"><label for="toggleTips" class="material-icons">school</label>&nbsp;
        <input type="checkbox" id="toggleStats" @click="$store.commit('toggleMetrics')"><label for="toggleStats" class="material-icons">bar_chart</label> -->
      </div>

      <div class="task-stats">
        <span>Зроблено {{ task.attempts }}&thinsp;/&thinsp;2 спроб</span>&ensp;&bull;&ensp;
        <span>Отримано {{ task.completed ? task.score : 0 }}&thinsp;/&thinsp;{{ task.weight }} балів</span>
      </div>

      <div class="task-tip" v-if="task?.tip" v-show="$store.state.showTips" :style="{ background: colorTheme+'15' }">
        <div>
          <div v-html="task.tip.text"></div>
          <div v-if="task.tip?.formula" v-html='"<br/>$$"+task.tip.formula+"$$"'></div>
          <div v-if="task.tip?.note">
            <br/><span class="tag1" style="background:orange">Зверніть увагу</span>&ensp;
            {{ task.tip.note }}
          </div>
        </div>
        <img v-if='task.tip?.image' :src='`${$cloudinary}images/${task._id}t.png`'>
      </div>

      <div class="task-text" v-html="task.text"></div>

      <div class="task-content">
        <div class="task-options" v-if='task.items'>
          <div v-for='(item, index) in task.items' :key='index'>
            <span class="task-letter">{{ index + 1 }}</span>
            <span class="task-option" v-if='item !== "image"'>{{ item }}</span>
            <img v-else :src='`${$cloudinary}images/${task._id}-${index + 1}.png`'>
          </div>
        </div>
        <div class="task-options" v-if='task.options'>
          <div v-for='(option, index) in task.options' :key='index'>
            <span class="task-letter">{{ String.fromCharCode(1040 + index) }}</span>
            <span class="task-option" v-if='option !== "image"'>{{ option }}</span>
            <img v-else :src='`${$cloudinary}images/${task._id}${String.fromCharCode(97 + index)}.png`'>
          </div>
        </div>
        <img class="task-image" v-if='task.image' :src='`${$cloudinary}images/${task._id}.png`'>
      </div>

      <div class="task-panel" :style="{ display: ((task.completed && task.percentage==100) || task.attempts==$maxAttempts) ? 'flex' : 'block' }">
        <div class="task-blank">
          <answer-blank :type='task.type' 
           :disabled='task.completed' 
           :data='task.answer' 
           :correctData='task.completed ? task.key : undefined' 
           :showCorrect="task.attempts==$maxAttempts">
            <template v-for="i in task.key.length" v-slot:[`label${i}`]>
              <div class="bar-wrapper" 
               v-if="task.type=='radio' && ((task.completed && task.percentage==100) || task.attempts==$maxAttempts) && task.metrics[i-1].some(e => e>0)"
               v-show="$store.state.showMetrics">
                <chart-column :data="task.metrics[i-1]" :colors="metricsColors(i-1)" :gap="5"/>
              </div>
            </template>
          </answer-blank>
          <div class='input-show-correct'
           v-if='task.completed && task.attempts>0 && task.attempts<$maxAttempts && task.percentage<100' 
           @click="modalConfirmationVisible = true">Показати правильну відповідь</div>
          <div class='input-error-message' v-if='v$.$error'>Неправильний формат відповіді</div>
          <div class="show-comment" v-if="task.finished && task?.comment && !showComment" @click="showComment=true">Натисніть, щоб побачити розв'язок</div>
        </div>
        <div class="task-buttons">
          <button class='button-plain' v-if="task.completed && task.percentage<100 && task.attempts<$maxAttempts" @click="() => {task.completed = false}">Нова спроба</button>
          <button class='button-plain' v-if="!task.completed" :disabled="!task.answered" @click='postAnswer()'>Відповісти</button>
          <button id='button-back' class='button-plain button-next' :disabled="taskIndex == 0" @click='taskIndex--'>Назад</button>
          <button class='button-plain button-next' v-if="taskIndex !== lesson.length-1" @click='taskIndex++'>Далі</button>
          <button class='button-plain' v-else :disabled="!lessonCompleted" @click='modalResultVisible = true'>Результат</button>
        </div>
      </div>
      
      <div class="task-comment" v-if="task?.comment" v-show="showComment">
        <b>Розв'язок:</b>&ensp;
        <span v-html="task.comment"></span>
      </div>

    </div>
  </template>

</main-layout>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import controller from '@/api/controller'
import mainLayout from '@/layouts/main-layout.vue'
import pagination from '@/components/pagination.vue'
import answerBlank from '@/components/answer-blank.vue'
import modal from '@/components/modal-basic.vue'
import modalReport from '@/components/modal-report.vue'
import chartColumn from '../components/charts/chart-column.vue'
import chartDoughnut from '../components/charts/chart-doughnut.vue'
const format = (arr) => {return arr.every(e => /^\-?\d{1,4}(\,\d{1,2})?$/.test(e))}
export default {
  components: { mainLayout, pagination, answerBlank, modal, modalReport, chartColumn, chartDoughnut },
  beforeMount() { 
    this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]))
  },
  data() { return {
    v$: useVuelidate(),
    taskIndex: 0,
    showResult: false,
    showComment: false,
    modalConfirmationVisible: false,
    modalResultVisible: false,
    modalFeedbackVisible: false,
    moduleName: localStorage.getItem('moduleName'),
    lessonName: localStorage.getItem('lessonName'),
    blockName: localStorage.getItem('blockName'),
    moduleId: localStorage.getItem('moduleId'),
    lessonId: parseInt(localStorage.getItem('lessonId')),
    blockId: parseInt(localStorage.getItem('blockId')),
    colorTheme: localStorage.getItem('colorTheme'),
  }},
  computed: {
    lesson() { return this.$store.state.tasks },
    task() { return this.lesson[this.taskIndex] },
    lessonCompleted() { return !this.lesson.map(e => e.completed).includes(false) },
    lessonPercentage() { return Math.round(this.lesson.map(e => e.score).sum() / this.lesson.map(e => e.weight).sum() * 100) },
    paginationColors() { return this.lesson.map(task => task.completed ? this.mix3(task.percentage) : 'gray') },
    backgroundURL() {return this.$cloudinary + this.moduleId + '.jpg' },
  },
  validations() { return {
    task: {answer: { format }}
  }},
  watch: {
    taskIndex: function(val) {
      this.v$.$reset()
      this.showComment = false
      this.$nextTick( _ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]) )
    },
  },
  methods: {
    metricsColors(i) { return ['А','Б','В','Г','Д'].map(e => e == this.task.key[i] ? this.$green : e == this.task.answer[i] ? this.$red : '#0004') },
    postAnswer() {
      if (this.task.type == "input") this.v$.$touch()
      if (!this.v$.$error) {
        controller.postAnswer(this.moduleId, this.blockId, this.lessonId, this.task._id, this.task.answer, this.task.score, this.task.weight)
        this.task.completed = true
        this.task.attempts ++
        this.modalConfirmationVisible = false
      }
    },
    reportMistake() {
      controller.reportMistake()
    }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
$mobile: 639px;

input[type='checkbox'] {
  appearance: none;
  & + label { color: #0008 }
  &:checked + label { color: #0002 }
}

.link { font-size: .9em }
.chart-percentage { font-size: 2.5em }
.chart-wrapper {
  width: 13em;
  height: 13em;
  margin: 0 auto;
}
.input-textfield-line {
  min-width: 100%;
  max-width: 100%;
  margin-top: .5em;
}
.input-error-message { margin-top: .5em }
.input-show-correct {
  margin-top: .7em;
  font-size: .85em;
  &:hover { text-decoration: underline; }
}

.header-section {
  width: min(75em,100%);
  margin: auto;
  padding-bottom: 1em;
  .lesson-navbar {
    display: flex;
    align-items: center;
    margin-bottom: 12vmin;
    //font-size: .9em;
    color: #fff;
    *:last-child { margin-left: auto }
  }
  .lesson-name {
    display: inline-block;
    margin-bottom: 6vmin;
    font-size: clamp(1.5em, 4.2vmin, 2em);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1.5em;
    color: #fff;
  }
}

.task-section {
  width: min(75em,100%);
  margin: auto;
  padding: 1.75em 2em 2.25em 2em;
  margin-bottom: 3em;
  box-sizing: border-box;
  background: white;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,.12);
}

.task-info {
  display: flex;
  align-items: center;
  column-gap: 10px;
  //*:last-child { margin-left: auto }
  .material-icons-outlined { font-size: 1.25em }
  .task-index { 
    font-weight: bold; 
    font-size: 1.25em;
  }
  .task-complexity {
    display: inline-block;
    width: 1.25em;
    line-height: 1.25em;
    border: 2px solid;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    font-weight: bold;
  }
}

.task-stats {
  * { display: inline-block }
  margin-top: 7px;
  margin-bottom: 1.5em;
  padding-bottom: 1.5em;
  border-bottom: 1px solid #0002;
  line-height: 1.5em;
  font-size: .9em;
  color: #0008;
}

.task-tip {
  display: flex;
  gap: 1em;
  justify-content: center;
  margin: 0 -2em;
  margin-bottom: 1.25em;
  padding: 1.25em 2em;
  box-sizing: border-box;
  line-height: 1.5em;
  @media (max-width: 640px) { flex-wrap: wrap }
  div { flex-grow: 1 }
  img {
    flex-grow: 0;
    height: 10em;
  }
}

.task-text { 
  margin-bottom: 20px;
  line-height: 1.5em; 
}

.task-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
  .task-image {
    flex-grow: 0;
    height: 12em;
    object-fit: contain;
    margin-top: 10px;
  }
  .task-options { 
    flex-grow: 1 
  }
  .task-option {
    display: inline-block;
    padding: .55em 0;
    line-height: 1.5em;
  }
  .task-letter {
    margin-left: 1ch;
    margin-right: 2ch;
    font-size: 1.1em;
    font-weight: bold;
    vertical-align: middle;
  }
  img {
    //height: 12em;
    margin: 10px 0;
    vertical-align: middle;
  }
}

.task-panel {
  flex-wrap: wrap;
  align-items: bottom;
  padding-top: 1.5em;
  .task-buttons {
    margin-top: 1.5em;
    gap: 15px;
    display: flex;
    align-items: flex-end;
    @media (max-width: 600px) { 
      flex-basis: 100%;
      align-items: stretch;
    }
    button:first-child { margin-right: auto; }
    #button-back { @media (max-width: 640px) { display: none } }
    button { @media (max-width: 640px) { flex-basis: 100%; } }
  }
  .task-blank { 
    margin-right: auto;
  }
  .bar-wrapper {
    height: 3em;
    margin: .5em 0;
  }
  .show-correct {
    margin-top: .5em;
    font-size: .9em;
    &:hover { opacity: .5; }
  }
}

.show-comment {
  margin-top: .7em;
  font-size: .85em;
  color: #000a;
  &:hover { text-decoration: underline; }
}
.task-comment {
  margin-top: 1.5em;
  padding-top: 1.5em;
  line-height: 1.5em;
  border-top: 1px solid #0002;
  b {
    vertical-align: middle;
    display: inline-block;
    margin: 10px 0;
  }
  span {
    vertical-align: middle;
    display: inline-block;
  }
}
</style>